<template>
    <div class="myFindack">
        <!-- 顶部导航 -->
        <van-nav-bar class="loginBar" :border='false'>
               <template #left>
                    <van-icon name="arrow-left" color="#727272" size="24px" @click="getBack"/>
               </template>
               <template #title>
                   <span class="title">
                          重置密码
                   </span>
               </template>

               <template #right>
                   <div class="navigation">
                       <span class="iconfont icon-sangedian" ></span>
                       <span class="iconfont icon-tongxinyuan" @click="getPage('Home')"></span>
                   </div>
               </template>
        </van-nav-bar>

        <!-- logo图标 -->
        <div  class="iconfont icon-icon_ruixingkafei1 logins"></div>
        <p><span></span></p>

        <!-- 登录表单 -->
        <van-form  > 
            <!-- 输入框 -->
            <van-field
                v-model="newObj.phone"
                class="froms01"
                placeholder="手机号"
            />
            <van-field
                v-model="newObj.password"
                class="froms02"
                :type="eye01 ? 'text' : 'password'"
                placeholder="请设置新密码"
                :right-icon="eye01 ? 'eye-o'  : 'closed-eye'"
                @click-right-icon="isTag" 
                
            />

           <!-- 登录键 -->
            <div style="margin: 20px;">
                <van-button round block type="info" native-type="submit" color="#bfc9e2" @click='ff_login'>
                 提交
                </van-button>
            </div>
        </van-form>
 
    </div>
</template>

<script>
    import '../assets/font/iconfont.css'
    import {inspectFrom} from '../assets/js/inspectFrom/inspectFrom'
    export default {
         name   : 'Pass',
         data() {
             return {
                     // 老用户登录
                    newObj:{
                        phone:null,
                        password:null,
                    }, 
                    eye01 : true
                    
             }
         },
         methods:{
             getBack(){
                 this.$router.go(-1);
             },
              getPage(name){
                    this.$router.push({name});
            },
             // 登录注册密码的闭眼的方法
            isTag(){
                this.eye01 =  !this.eye01;
            },
            // 登录的方法
            ff_login(){
                let obj = {
                    phone:{
                        value    :  this.newObj.phone,
                        errorMsg :  '手机号格式错误',
                        reg      :  /^1[3-9]\d{9}$/
                    },
                    password:{
                        value    :  this.newObj.password,
                        errorMsg :  '密码格式错误',
                        reg      :  /^[A-Za-z]\w{5,15}$/
                    },
                }
            let isTest = inspectFrom.inspect(obj);
                if(isTest){
                    let useObj = {};
                    Object.assign(useObj,this.newObj);
                    useObj.appkey = this.appkey;
                    this.$toast.loading({
                            message: '加载中...',
                            // 禁止点击背景
                            forbidClick: true,
                            loadingType: 'spinner',
                        });

                    this.axios({
                        url:'/retrievePassword',
                        method:'POST',
                        data:useObj
                    })
                    .then(res => {
                        if(res.data.code == "L001"){
                            this.$toast(res.data.msg);
                            setTimeout(()=>{
                                this.$router.push({name:'Login'});
                            },500);
                        }else{
                            this.$toast(res.data.msg);
                        }
                    })
                    .catch(err =>{

                    })
            }

            },
            

         }
    }
</script>

<style lang="less" scoped>
.myFindack{
    width: 100%;
    height: 100%;
    background-color: #f5f5f5;

    /deep/ .loginBar{
        background-color: #f5f5f5;
        font-weight: 550;


      
    }
    .logins{
        font-size: 230px;
        color:#4E6EB0;
    }
    .navigation{
         background-color: #fff;
         padding: 0px 6px;
         border-radius: 20px;
         position: relative;
         right: -9px;
         box-shadow: 1px 1px 8px 1px #ccc;
         opacity: 0.7;


         >span:nth-child(1)::after{
             content:  '';
             width: 2px;
             height: 20px;
             border-left: 1.5px solid #e8e8e8;
             position: absolute;
             top: 1px;
             left: 30px;
             
            
             
         }
         span{
             cursor: pointer;
             font-size: 22px;
             margin: 10px 8px;
             position: relative;
         }
    }
    .title{
        font-size: 17px;
    }

    /deep/ .imgs{
        margin-top: 20px;
    }

    /deep/.van-form{
        width: 95%;
        margin: 0px auto;
    }

     
}

</style>